<template>
  <PopUp :flow="tjsbStatus">
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click="closeTjsb">&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62f;</i>
          <span class="title-span">{{ $t("device.tjsb") }}</span>
        </div>
        <div class="content">
          <div class="sb-form form">
            <div class="form-item">
              <div class="form-item-name">
                <span>{{ $t("device.sblx") }}：</span>
              </div>
              <div class="form-item-value">
                <el-select
                  class="select"
                  style="width:145px"
                  v-model="newDevice.machineTypeId"
                  filterable
                  :placeholder="$t('message.select')"
                  @change="selectMachineType"
                >
                  <el-option
                    v-for="item in machineTypeList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{ $t("device.sbxh") }}：</span>
              </div>
              <div class="form-item-value">
                <el-select
                  class="select"
                  style="width:145px"
                  v-model="newDevice.machineModelId"
                  filterable
                  :placeholder="$t('message.select')"
                  @change="selectMachineModel"
                >
                  <el-option
                    v-for="item in machineModelList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{ $t("device.sbbh") }}：</span>
              </div>
              <div class="form-item-value">
                <el-input
                  v-model="newDevice.machineNo"
                  style="width:145px"
                  class="select"
                  :placeholder="$t('message.fill')"
                ></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{ $t("device.sn") }}：</span>
              </div>
              <div class="form-item-value">
                <el-input
                  v-model="newDevice.machineNum"
                  style="width:145px"
                  class="select"
                  :placeholder="$t('message.fill')"
                ></el-input>
              </div>
            </div>
            <div
              class="form-item"
              v-if="isXd == 1 || (isXd == 2 && newDevice.machineModelId == 12)"
            >
              <div class="form-item-name">
                <span>{{ $t("device.mac") }}：</span>
              </div>
              <div class="form-item-value">
                <el-input
                  v-model="newDevice.machineMac"
                  style="width:145px"
                  class="select"
                  :placeholder="$t('message.fill')"
                ></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{ $t("device.syks") }}：</span>
              </div>
              <div class="form-item-value">
                <el-select
                  class="select"
                  style="width:145px"
                  v-model="newDevice.departmentId"
                  filterable
                  :placeholder="$t('message.select')"
                >
                  <el-option
                    v-for="item in departmentList"
                    :key="item.departmentId"
                    :label="item.name"
                    :value="item.departmentId"
                  >
                  </el-option>
                </el-select>
              </div>
            </div>
          </div>
          <div class="form-img">
            <img src="../../assets/img/monitor/case1.png" alt="" v-if="isXd==0&&language=='zh'">
            <img src="../../assets/img/monitor/case-en.png" alt="" v-if="isXd==0&&language=='en'">
            <img src="../../assets/img/device/tpec-case.png" alt="" v-if='isXd==1'>
            <img src="../../assets/img/device/xy.png" alt="" v-if='isXd==2 && newDevice.machineModelId==10'>
            <img src="../../assets/img/device/xy02.png" alt="" v-if='isXd==2 && newDevice.machineModelId==12'>
            <img src="../../assets/img/device/lc.png" alt="" v-if='isXd==3 && newDevice.machineModelId==11'>
            <img src="../../assets/img/device/htjr.png" alt="" v-if='isXd==3&& newDevice.machineModelId==15'>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click="addDeviceFn">{{
            $t("button.confirm")
          }}</a>
          <a href="javascript:void(0)" class="off" @click="closeTjsb">{{
            $t("button.close")
          }}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from "../../components/popUp";
import { getMachineModel, addDevice } from "@/api/device";
import {getDepartmentListD} from '@/api/monitor'
var that = {};
export default {
  name: "ghsb",
  data() {
    return {
      newDevice: {
        machineTypeId: "",
        machineModelId: "",
        machineNo: "",
        machineNum: "",
        machineMac: "",
        departmentId: ""
      },
      machineNumList: [],
      isXd: "",
      machineModelList: [],
      language: that.$store.state.login.language,
      departmentList:[]
    };
  },
  props: {
    machineTypeList: {
      type: Array,
      default: function() {
        return [];
      }
    },
    tjsbStatus: {
      type: Boolean
    },
    getDeviceListFn: {
      type: Function,
      default: null
    }
  },
  beforeCreate() {
    that = this;
  },
  mounted: function() {
    that.newDevice.machineTypeId = that.machineTypeList[0].id;
    that.getMachineModelFn();
    that.isXd = that.machineTypeList[0].typeCode;
    getDepartmentListD().then((res)=>{
      if(res.code==200 && res.success){
        that.departmentList = res.data;
      }
    })
  },
  methods: {
    /*查询设备型号*/
    getMachineModelFn() {
      var data = {
        machineTypeId: that.newDevice.machineTypeId
      };
      getMachineModel(data).then(res => {
        console.log(res);
        if (res.code == 200 && res.success) {
          that.machineModelList = res.data;
          that.newDevice.machineModelId = that.machineModelList[0].id;
        }
      });
    },
    selectMachineType(id) {
      that.newDevice.machineTypeId = id;
      that.machineTypeList.map(item => {
        if (item.id == id) {
          that.isXd = item.typeCode;
        }
      });
      that.getMachineModelFn();
    },
    selectMachineModel(id) {
      that.newDevice.machineModelId = id;
    },
    closeTjsb() {
      that.$emit("gettjsbStatus", !that.tjsbStatus);
    },
    /*添加设备*/
    addDeviceFn() {
      if (
        that.isXd == 1 ||
        (that.isXd == 2 && that.newDevice.machineModelId == 12)
      ) {
        if (that.newDevice.machineTypeId == "") {
          that.error(that.$t("device.err.sbmcnull"));
        } else if (that.newDevice.machineModelId == "") {
          that.error(that.$t("device.err.sbxhnull"));
        } else if (that.newDevice.machineNo == "") {
          that.error(that.$t("device.err.sbbhnull"));
        } else if (that.newDevice.machineNum == "") {
          that.error(that.$t("device.err.snnull"));
        } else if (that.newDevice.machineMac == "") {
          that.error(that.$t("device.err.macnull"));
        } else if (that.newDevice.departmentId == "") {
          that.error(that.$t("device.err.ksnull"));
        } else {
          var data = {
            machineTypeId: that.newDevice.machineModelId,
            machineNum: that.newDevice.machineNum,
            machineNo: that.newDevice.machineNo,
            machineMac: that.newDevice.machineMac,
            departmentId: that.newDevice.departmentId
          };
          addDevice(data).then(res => {
            if (res.code == 200 && res.success) {
              that.success(that.$t("message.OperSuccess"));
              that.$emit("gettjsbStatus", !that.tjsbStatus);
              that.getDeviceListFn();
              that.newDevice = {
                machineTypeId: "",
                machineModelId: "",
                machineNo: "",
                machineNum: "",
                machineMac: "",
                departmentId: ""
              };
            }
          });
        }
      } else {
        if (that.newDevice.machineTypeId == "") {
          that.error(that.$t("device.err.sbmcnull"));
        } else if (that.newDevice.machineModelId == "") {
          that.error(that.$t("device.err.sbxhnull"));
        } else if (that.newDevice.machineNo == "") {
          that.error(that.$t("device.err.sbbhnull"));
        } else if (that.newDevice.machineNum == "") {
          that.error(that.$t("device.err.snnull"));
        } else if (that.newDevice.departmentId == "") {
          that.error(that.$t("device.err.ksnull"));
        } else {
          var data = {
            machineTypeId: that.newDevice.machineModelId,
            machineNum: that.newDevice.machineNum,
            machineNo: that.newDevice.machineNo,
            departmentId: that.newDevice.departmentId
          };
          addDevice(data).then(res => {
            if (res.code == 200 && res.success) {
              that.success(that.$t("message.OperSuccess"));
              that.getDeviceListFn();
              that.$emit("gettjsbStatus", !that.tjsbStatus);
              that.newDevice = {
                machineTypeId: "",
                machineModelId: "",
                machineNo: "",
                machineNum: "",
                machineMac: "",
                departmentId: ""
              };
            }
          });
        }
      }
    }
  },
  components: {
    PopUp: popUp
  }
};
</script>
<style scoped lang="scss">
.content {
  margin: 0 42px;
  display: flex;
  flex-direction: row;

  .sb-form {
    width: 363px;
    float: left;
    padding-bottom: 20px;
    .form-item {
      display: flex;
      margin-top: 24px;
      .form-item-name {
        width: 45%;
        span {
          font-size: 16px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: rgba(51, 51, 51, 1);
          float: right;
          display: block;
          line-height: 36px;
        }
      }
      .form-item-value {
        width: 59%;
        span {
          font-size: 16px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: rgba(51, 51, 51, 1);
          float: left;
          display: block;
          line-height: 36px;
          i {
            font-style: normal;
            color: #1890ff;
          }
        }
        .select {
          float: left;
        }
      }
    }
  }
  .form-img {
    float: left;
    margin-right: 47px;
    display: flex;
    align-items: center;
    img {
      width: 202px;
      float: left;
    }
    img:hover {
      transform: scale(1.5);
      -webkit-transform: scale(1.5);
      -moz-transform: scale(1.5);
      -o-transform: scale(1.5);
    }
  }
}
.form-item-value /deep/ .el-input__inner {
  height: 36px;
}
</style>



