<template>
  <PopUp :flow='fpsbStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeFpsb'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62f;</i>
          <span class="title-span">{{$t("device.fpsb")}}</span>
        </div>
        <div class="department">
          <span>{{$t("device.stjks")}}:</span>
          <el-select class='select' v-model='condition.departmentId' filterable :placeholder="$t('message.select')" >
           <el-option
             v-for="item in departmentList"
             :key="item.departmentId"
             :label="item.name"
             :value="item.departmentId">
           </el-option>
         </el-select>
        </div>
        <div class="content">
          <el-checkbox-group v-model="checkList">
            <el-checkbox :label="item.value" v-for='(item,index) in noDistributionList' :key="index" border></el-checkbox>
          </el-checkbox-group>
        </div>
        <span class='note'>{{ $t("device.zsb") }}</span>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='distributeFn'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeFpsb'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {getDistributionDevice,distributionDevice } from '@/api/device'
import {getDepartmentListD} from '@/api/monitor'
var that= {};
  export default {
    name: 'fpsb',
      data(){
          return {
            departmentList:[],
            condition:{
              departmentId:'',
              machineIds:[]
            },
            checkList:[],
            noDistributionList:[]
          }
      },
      props:{
        fpsbStatus:{
          type:Boolean
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        getDepartmentListD().then((res)=>{
          if(res.code==200 && res.success){
            that.departmentList = res.data;
            that.condition.departmentId = that.departmentList[0].departmentId;
          }
        });
        that.getNoDistribution();
      },
      methods:{
        // selectDepartment(department){
          // that.condition.departmentId = departmentId;
        // },
        closeFpsb(){
          that.$emit('getfpsbStatus',!that.fpsbStatus);
        },
        getNoDistribution(){
          getDistributionDevice().then((res)=>{
            if(res.code==200 && res.success){
              that.noDistributionList = res.data;
              console.log(that.noDistributionList)
            }
          })
        },
        distributeFn(){
          console.log(that.checkList)
          console.log(that.noDistributionList)
          that.condition.machineIds = [];
          if(!that.condition.departmentId){
            that.error(that.$t("device.err.ksnull"))
          }else if(that.checkList.length == 0){
            that.error(that.$t("device.err.qxssb"))
          }else{
            for(var i=0;i<that.noDistributionList.length;i++){
              for(var j=0;j<that.checkList.length;j++){
                if(that.noDistributionList[i].value == that.checkList[j]){
                  that.condition.machineIds.push(that.noDistributionList[i].id);
                }
              }
            }
            var data=that.condition;
            console.log(data)
            distributionDevice(data).then((res)=>{
              if(res.code==200 && res.success){
                that.success(that.$t("device.err.fpsuccess"))
                that.$emit("getfpsbStatus",!that.fpsbStatus,1);
              }
            })
          }
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.department{
  display:flex;
  justify-content: flex-start;
  margin: 0 42px 26px;
  align-items: center;
}
.content{
  margin:0 42px;
  display:flex;
  flex-direction: row;
  width:690px;
  height:250px;
  .el-checkbox-group{
    width:100%;
    overflow-y: scroll;
    flex-wrap:wrap;
    padding:20px;
    display:flex;
    justify-content: flex-start;
    /deep/ .el-checkbox{
      width:108px;
      height:40px;
      display:flex;
      justify-content: center;
      align-items: center;
      margin:0 22px 20px 0;
    }
  }
}
.note{
  margin:10px 42px 0;
  font-size: 16px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #A4A9B6;
  line-height: 22px;
  display:block;
  width:672px;
  text-align:left;
}
::-webkit-scrollbar {
  display:none
}
</style>



