<template>
  <PopUp :flow='xgsbStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeXgsb'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62f;</i>
          <span class="title-span">{{$t("device.xgsb")}}</span>
        </div>
        <div class="content xgsb-content">
          <div class="sb-form form">
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("device.sbbh")}}：</span>
              </div>
              <div class="form-item-value">
                <el-input v-model="oldDevice.machineNo" style='width:145px' class='select' :placeholder="$t('message.fill')"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("device.sbmc")}}：</span>
              </div>
              <div class="form-item-value">
                <span>{{oldDevice.machineName}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("device.sbxh")}}：</span>
              </div>
              <div class="form-item-value">
                <span>{{oldDevice.machineModel}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("device.sn")}}：</span>
              </div>
              <div class="form-item-value">
                <el-input v-model="oldDevice.machineNum" style='width:145px' class='select' :placeholder="$t('message.fill')"></el-input>
              </div>
            </div>
            <div class="form-item" v-if="isXd == 1 || (isXd == 2 && oldDevice.machineTypeIdChild == 12)">
              <div class="form-item-name">
                <span>{{$t("device.mac")}}：</span>
              </div>
              <div class="form-item-value">
                <el-input v-model="oldDevice.machineMac" style='width:145px' class='select' :placeholder="$t('message.fill')"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("device.glks")}}：</span>
              </div>
              <div class="form-item-value">
                <el-select class='select' v-model='oldDevice.departmentId' filterable :placeholder="$t('message.select')" @change='selectDepartment'>
                 <el-option
                   v-for="item in departmentList"
                   :key="item.departmentId"
                   :label="item.name"
                   :value="item.departmentId">
                 </el-option>
               </el-select>
              </div>
            </div>
          </div>
          <!-- <div class="form-img">
            <img src="../../assets/img/device/tpec-casezr.png" alt="">
          </div> -->
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='editDeviceFn'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeXgsb'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {getMachineModel,editDevice } from '@/api/device'
import {getDepartmentListD} from '@/api/monitor'
var that= {};
  export default {
    name: 'xgsb',
      data(){
          return {
            oldDevice:{
            },
            machineNumList:[],
            isXd:'',
            machineModelList:[],
            language:that.$store.state.login.language,
            departmentList:[]
          }
      },
      props:{
        machineTypeList: {
            type: Array,
            default: function () {return []}
        },
        nowDevice:{
          type:Object
        },
        xgsbStatus:{
          type:Boolean
        },
        getDeviceListFn:{
          type:Function,
          default: null
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        console.log(that.nowDevice)
        console.log(that.machineTypeList)
        // that.nowDevice.machineTypeId = that.machineTypeList[0].id;
        that.isXd = that.nowDevice.typeCode;
        // that.getMachineModelFn();
        that.oldDevice = JSON.parse(JSON.stringify(that.nowDevice))
        that.getDepartmentList();
      },
      methods:{
        /*查询设备型号*/
        // getMachineModelFn(){
        //   var data={
        //     machineTypeId:that.newDevice.machineTypeId
        //   }
        //   getMachineModel(data).then((res)=>{
        //      console.log(res);
        //     if(res.code==200 && res.success){
        //       that.machineModelList = res.data;
        //       that.newDevice.machineModelId = that.machineModelList[0].id;
        //     }
        //   })
        // },
        // selectMachineType(id){
        //   that.newDevice.machineTypeId = id;
        //   that.machineTypeList.map((item) => {
        //     if(item.id==id){
        //       that.isXd = item.typeCode;
        //     }
        //   })
        //   that.getMachineModelFn();
        // },
        // selectMachineModel(id){
        //   that.newDevice.machineModelId = id;
        // },
        selectDepartment(departmentId){
          that.oldDevice.departmentId = departmentId
        },
        /*查询科室*/
        getDepartmentList(){
          var data = {
            flag:true
          }
          getDepartmentListD(data).then((res)=>{
            if(res.code==200 && res.success){
              that.departmentList = res.data;
            }
          });
        },
        closeXgsb(){
          that.$emit('getxgsbStatus',!that.xgsbStatus);
        },
        /*编辑设备*/
        editDeviceFn(){
          if (that.isXd == 1 ||(that.isXd == 2 && that.oldDevice.machineTypeIdChild == 12)){
            if(that.oldDevice.machineNo==''){
              that.error(that.$t("device.err.sbbhnull"))
            }else if(that.oldDevice.machineNum==''){
              that.error(that.$t("device.err.snnull"))
            }else if(that.oldDevice.machineMac==''){
              that.error(that.$t("device.err.macnull"))
            }else{
              console.log(that.oldDevice)
              var data={
                machineId:that.oldDevice.machineId,
                machineSn:that.oldDevice.machineNum,
                machineNo:that.oldDevice.machineNo,
                machineMac:that.oldDevice.machineMac,
                departmentId:that.oldDevice.departmentId
              }
              editDevice(data).then((res)=>{
                if(res.code==200 && res.success){
                  that.success(that.$t("message.OperSuccess"));
                  that.$emit('getxgsbStatus',!that.xgsbStatus);
                  that.getDeviceListFn();
                  that.oldDevice = {}
                }
              })
            }
          }else{
            if(that.oldDevice.machineNo==''){
              that.error(that.$t("device.err.sbbhnull"))
            }else if(that.oldDevice.machineNum==''){
              that.error(that.$t("device.err.snnull"))
            }else{
              console.log(that.oldDevice)
              var data={
                machineId:that.oldDevice.machineId,
                machineSn:that.oldDevice.machineNum,
                machineNo:that.oldDevice.machineNo,
                departmentId:that.oldDevice.departmentId
              }
              editDevice(data).then((res)=>{
                if(res.code==200 && res.success){
                  that.success(that.$t("message.OperSuccess"));
                  that.$emit('getxgsbStatus',!that.xgsbStatus);
                  that.getDeviceListFn();
                  that.oldDevice = {}
                }
              })
            }
          }

        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  display:flex;
  flex-direction: row;

  .sb-form{
    // width:363px;
    width:672px;
    float:left;
    padding-bottom:20px;
    .form-item{
      display:flex;
      margin-top:24px;
      .form-item-name{
        width:38%;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:right;
          display:block;
          line-height:36px;
        }
      }
      .form-item-value{
        width:59%;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:left;
          display:block;
          line-height:36px;
          i{
            font-style: normal;
            color:#1890FF;
          }
        }
        .select{
          float:left;
        }
      }
    }

  }
  .form-img{
    float:left;
    margin-right:47px;
    display:flex;
    align-items: center;
    img{
      width:202px;
      float:left;
    }
    img:hover{
      transform:scale(1.5);
      -webkit-transform:scale(1.5);
     -moz-transform:scale(1.5);
     -o-transform:scale(1.5);
    }
  }
}
.xgsb-content{
    margin:0 128px;
}
.form-item-value /deep/ .el-input__inner{
  height:36px;
  width:273px;
}
</style>



